export default function selectionMap({series,films}){
    return {
        series: [
            {
                title: 'Documentaries',
                data: series.filter(item => item.genre === 'documentaries')
            },
            {
                title: 'Comedies',
                data: series.filter(item => item.genre === 'comedies')
            },
            {
                title: 'Children',
                data: series.filter(item => item.genre === 'children')
            },
            {
                title: 'Crime',
                data: series.filter(item => item.genre === 'crime')
            },
            {
                title: 'Feel Good',
                data: series.filter(item => item.genre === 'feel-good')
            }
        ],
        films: [
            {
                title:'Drama',
                data: films.filter(el => el.genre === 'drama')
            },
            {
                title:'Thriller',
                data: films.filter(el => el.genre === 'thriller')
            },
            {
                title:'Children',
                data: films.filter(el => el.genre === 'children')
            },
            {
                title:'Suspense',
                data: films.filter(el => el.genre === 'suspense')
            },{
                title:'Romance',
                data: films.filter(el => el.genre === 'romance')
            }
        ]
    }
}